@font-face {
	font-family: 'icomoon';
	src:url('../inc/fonts/icomoon/icomoon.eot?-bizbcp');
	src:url('../inc/fonts/icomoon/icomoon.eot?#iefix-bizbcp') format('embedded-opentype'),
		url('../inc/fonts/icomoon/icomoon.ttf?-bizbcp') format('truetype'),
		url('../inc/fonts/icomoon/icomoon.woff?-bizbcp') format('woff'),
		url('../inc/fonts/icomoon/icomoon.svg?-bizbcp#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
	font-family: 'icomoon';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

